import React from 'react'
import "./Sidebar.css"
import {
    LineStyle, Timeline, TrendingUp, PermIdentity, Storefront, AttachMoney, BarChart, MailOutline, DynamicFeed,
    ChatBubbleOutline, WorkOutline, Report
} from "@material-ui/icons"
import { Link } from 'react-router-dom'

function Sidebar() {
    return (
        <div className="sidebar">
            <div className="sidebarWrapper">

                <div className="sidebarMenu">
                    <h3 className="sidebarTitle">Dashboard</h3>
                    <ul className="sidebarList">
                    <Link to="/" className="link">
                        <li className="sidebarListItem active"><LineStyle className="sidebarIcon" /> Home </li>
                        </Link>
                    </ul>
                    <ul className="sidebarList">
                        <li className="sidebarListItem"><Timeline className="sidebarIcon" /> Analytics </li>
                    </ul>
                    <ul className="sidebarList">
                        <li className="sidebarListItem"><TrendingUp className="sidebarIcon" /> Sales </li>
                    </ul>
                </div>

                <div className="sidebarMenu">
                    <h3 className="sidebarTitle">Quick Menu</h3>
                    <ul className="sidebarList">
                        <Link to="/users" className="link">
                            <li className="sidebarListItem "><PermIdentity className="sidebarIcon" /> Users </li>
                        </Link>
                    </ul>
                    <ul className="sidebarList">
                        <Link to="/products" className="link">
                            <li className="sidebarListItem"><Storefront className="sidebarIcon" /> Products </li>
                        </Link>
                    </ul>
                    <ul className="sidebarList">
                        <li className="sidebarListItem"><AttachMoney className="sidebarIcon" /> Transactions </li>
                    </ul>
                    <ul className="sidebarList">
                    <Link to="/report" className="link">
                        <li className="sidebarListItem"><BarChart className="sidebarIcon" /> Reports </li>
                    </Link>
                    </ul>
                </div>

                <div className="sidebarMenu">
                    <h3 className="sidebarTitle">Notifications</h3>
                    <ul className="sidebarList">
                    <Link to="/mail" className="link">
                        <li className="sidebarListItem"><MailOutline className="sidebarIcon" /> Mail </li>
                   </Link>
                    </ul>
                    <ul className="sidebarList">
                    <Link to="/feedback" className="link">
                        <li className="sidebarListItem"><DynamicFeed className="sidebarIcon" /> Feedback </li>
                    </Link>
                    </ul>
                    <ul className="sidebarList">
                        <li className="sidebarListItem"><ChatBubbleOutline className="sidebarIcon" /> Messages </li>
                    </ul>
                </div>

                <div className="sidebarMenu">
                    <h3 className="sidebarTitle">Staff</h3>
                    <ul className="sidebarList">
                        <li className="sidebarListItem "><WorkOutline className="sidebarIcon" /> Manage </li>
                    </ul>
                    <ul className="sidebarList">
                        <li className="sidebarListItem"><Timeline className="sidebarIcon" /> Analytics </li>
                    </ul>
                   { /*<ul className="sidebarList">
                        <li className="sidebarListItem"><Report className="sidebarIcon" /> Reports </li>
                    </ul>*/}
                </div>

            </div>
        </div>
    )
}

export default Sidebar
